import {post} from '@/index';
export default {
	data() {
		return {
			title: '新闻分享页面',
			content:"",
			ios:'',
			list:[],
			msg:'',
			id:'',
			unfold:true,
			shadow:false,
			flag:'',
		}
	},
	onLoad(e) {
		this.getinfo(e.articleId)
		this.id=e.articleId
	},
	methods: {
		jump:function(e){
			if(e){
				this.flag=e
			}
			this.shadow=true
		},
		cancel:function(){
			if(this.flag){
				this.getinfo(this.flag)
				uni.pageScrollTo({
				    scrollTop: 0,
				    duration: 0
				});
				this.unfold=true;
			}else{
				this.unfold=false
			}
			this.shadow=false;
			
			this.flag=''
		},
		confirm:function(){
			if(this.flag){
				this.download(this.flag)
			}else{
				this.download(this.id)
			}
			this.shadow=false;
			this.unfold=false;
			this.flag=''
		},
		getinfo(articleId){
			post('/api/article/info/info',{"articleId": articleId}).then((res)=>{
				if(res[1].data.code==200){
					this.content=res[1].data.data.content
				}else{
					this.msg=res[1].data.msg;
				}
				
			}).catch((res)=>{
				console.log("出错了")
			})
			post('/api/article/info/listBySimilar',{"vo": {"articleId": articleId,}}).then((res)=>{
				this.list=res[1].data.data.list
			}).catch((res)=>{
				console.log("出错了")
			})	
		},
		download:function(id){
			if(navigator.userAgent.match(/(iPhone|iPod|iPad);?/i)) {
				window.location.href='lovexiaodoggieye://newsDetails?id='+id;
				var url='lovexiaodoggieye://newsDetails?id='+id
				window.setTimeout(function() {
					uni.navigateTo({
						url:"iosDownload?url="+url
					})
				}, 200)
			}else if(navigator.userAgent.match(/android/i)) {
				window.location.href= 'doggieyeshare://type/explore?id='+id;
				var url='doggieyeshare://type/explore?id='+id;
				window.setTimeout(function() {
					window.location.href='https://a.app.qq.com/o/simple.jsp?pkgname=com.kaixun.doggieye'
				}, 200)
			}
		}
	}
}
